body {
  font-family: Arial, sans-serif;
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.app {
  padding: 20px;
}

.timer, .settings {
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 400px;
}

.time-left {
  font-size: 48px;
  font-weight: bold;
  margin: 20px 0;
}

.controls button {
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
}

.controls button:hover {
  background-color: #0056b3;
}

.settings label {
  display: block;
  margin: 10px 0;
}

.settings input {
  width: 60px;
  padding: 5px;
  margin-left: 10px;
}

.settings button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #28a745;
  color: #fff;
}

.settings button:hover {
  background-color: #218838;
}

@media (max-width: 600px) {
  .timer, .settings {
    width: 90%;
    margin: 10px auto;
    padding: 15px;
  }

  .time-left {
    font-size: 36px;
  }

  .controls button {
    padding: 8px 16px;
    font-size: 14px;
  }
}